import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
// swal
import swal from 'sweetalert';
// @mui
import {
    Box,
    Card,
    Grid,
    Alert,
    Typography,
    Radio,
    RadioGroup,
    FormControlLabel,
    FormControl,
    FormLabel,
    CircularProgress,
    Chip,
    Button,
    IconButton,
    Paper,
    Divider
} from '@mui/material';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import SaveIcon from '@mui/icons-material/Save';
import axios from '../../utils/axios';

// ----------------------------------------------------------------------
VirtualAccountSelection.propTypes = {
    setting: PropTypes.object,
};

export default function VirtualAccountSelection({ setting }) {
    const [loading, setLoading] = useState(true);
    const [saving, setSaving] = useState(false);
    const [locks, setLocks] = useState([]);
    const [defaultProvider, setDefaultProvider] = useState('');
    const [error, setError] = useState(null);

    const AccessToken = window.localStorage.getItem('accessToken');

    // Helper map for labels
    const providerLabels = {
        'xixapay_palmpay': 'Xixapay (Palmpay)',
        'xixapay_kolomonie': 'Xixapay (Kolomonie)',
        'monnify_moniepoint': 'Monnify (Moniepoint)',
        'paystack_wema': 'Paystack (Wema)',
        'paymentpoint_palmpay': 'PaymentPoint (Palmpay)'
    };

    // Fetch current status
    useEffect(() => {
        fetchStatus();
    }, []);

    const fetchStatus = async () => {
        try {
            setLoading(true);
            const [locksRes, statusRes] = await Promise.all([
                axios.get(`/api/secure/virtualaccounts/locks/${AccessToken}/habukhan/secure`),
                axios.get('/api/secure/virtualaccounts/status')
            ]);

            if (locksRes.data.status === 'success') {
                setLocks(locksRes.data.locks);
            }
            if (statusRes.data.status === 'success') {
                setDefaultProvider(statusRes.data.default_provider_key || 'xixapay_palmpay');
            }
        } catch (err) {
            console.error('Error fetching virtual account status:', err);
            setError('Failed to load settings. Please try again.');
        } finally {
            setLoading(false);
        }
    };

    const handleDefaultChange = async (event) => {
        const newDefaultKey = event.target.value;
        const [provider, accountType] = newDefaultKey.split('_');

        const lock = locks.find(l => l.provider === provider && l.account_type === accountType);
        if (lock && lock.is_locked) {
            swal('Error', 'Cannot set a locked provider as default. Please unlock it first.', 'error');
            return;
        }

        try {
            setSaving(true);
            await axios.post(`/api/secure/selection/virtualaccounts/${AccessToken}/habukhan/secure`, {
                default_provider: provider,
                default_account_type: accountType,
            });

            setDefaultProvider(newDefaultKey);
            swal('Success', `Default provider updated successfully`, 'success');
        } catch (err) {
            console.error('Error setting default provider:', err);
            swal('Error', 'Failed to set default provider', 'error');
        } finally {
            setSaving(false);
        }
    };

    const handleMove = (index, direction) => {
        const newLocks = [...locks];
        const item = newLocks[index];

        // Remove item from current position
        newLocks.splice(index, 1);
        // Insert at new position
        newLocks.splice(index + direction, 0, item);

        // Update sort_order based on new index
        const updatedLocks = newLocks.map((lock, idx) => ({
            ...lock,
            sort_order: idx
        }));

        setLocks(updatedLocks);
    };

    const saveOrder = async () => {
        try {
            setSaving(true);
            await axios.post(`/api/secure/virtualaccounts/locks/reorder/${AccessToken}/habukhan/secure`, {
                locks: locks
            });
            swal('Success', 'Provider order updated successfully', 'success');
        } catch (err) {
            console.error('Error updating order:', err);
            swal('Error', 'Failed to save order', 'error');
        } finally {
            setSaving(false);
        }
    };

    if (loading) {
        return (
            <Box display="flex" justifyContent="center" alignItems="center" minHeight="400px">
                <CircularProgress />
            </Box>
        );
    }

    return (
        <Grid container spacing={3}>
            <Grid item xs={12} md={10}>
                {error && <Alert severity="error" sx={{ mb: 3 }}>{error}</Alert>}

                <Card sx={{ p: 3 }}>
                    <FormControl component="fieldset" fullWidth>
                        <FormLabel component="legend">
                            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                                <Box>
                                    <Typography variant="h6">Virtual Account Configuration</Typography>
                                    <Typography variant="body2" color="text.secondary">
                                        Select default provider and arrange display order.
                                    </Typography>
                                </Box>
                                <Button
                                    variant="contained"
                                    startIcon={<SaveIcon />}
                                    onClick={saveOrder}
                                    disabled={saving}
                                >
                                    Save Order
                                </Button>
                            </Box>
                        </FormLabel>

                        <RadioGroup value={defaultProvider} onChange={handleDefaultChange}>
                            <Grid container spacing={2}>
                                {locks.map((lock, index) => {
                                    const value = `${lock.provider}_${lock.account_type}`;
                                    const label = providerLabels[value] || `${lock.provider} (${lock.account_type})`;

                                    return (
                                        <Grid item xs={12} key={lock.id || value}>
                                            <Paper
                                                variant="outlined"
                                                sx={{
                                                    p: 2,
                                                    display: 'flex',
                                                    alignItems: 'center',
                                                    bgcolor: defaultProvider === value ? 'action.selected' : 'transparent',
                                                    borderColor: defaultProvider === value ? 'primary.main' : 'divider'
                                                }}
                                            >
                                                <Box sx={{ display: 'flex', flexDirection: 'column', mr: 2 }}>
                                                    <IconButton
                                                        size="small"
                                                        disabled={index === 0 || saving}
                                                        onClick={() => handleMove(index, -1)}
                                                    >
                                                        <ArrowUpwardIcon fontSize="small" />
                                                    </IconButton>
                                                    <IconButton
                                                        size="small"
                                                        disabled={index === locks.length - 1 || saving}
                                                        onClick={() => handleMove(index, 1)}
                                                    >
                                                        <ArrowDownwardIcon fontSize="small" />
                                                    </IconButton>
                                                </Box>

                                                <FormControlLabel
                                                    value={value}
                                                    control={<Radio disabled={saving || Boolean(lock.is_locked)} />}
                                                    label={
                                                        <Box sx={{ ml: 1 }}>
                                                            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                                                                <Typography variant="subtitle1" sx={{ fontWeight: 'bold' }}>
                                                                    {label}
                                                                </Typography>
                                                                {Boolean(lock.is_locked) && (
                                                                    <Chip label="Locked" size="small" color="error" variant="outlined" />
                                                                )}
                                                                {defaultProvider === value && (
                                                                    <Chip label="Default" size="small" color="primary" />
                                                                )}
                                                            </Box>
                                                            <Typography variant="caption" color="text.secondary">
                                                                Priority: {index + 1}
                                                            </Typography>
                                                        </Box>
                                                    }
                                                    sx={{ flex: 1, m: 0 }}
                                                />
                                            </Paper>
                                        </Grid>
                                    );
                                })}
                            </Grid>
                        </RadioGroup>
                    </FormControl>

                    <Alert severity="info" sx={{ mt: 3 }}>
                        <Typography variant="body2">
                            <strong>Note:</strong>
                            <br />- The <strong>Default</strong> provider is the primary account shown to users.
                            <br />- Use the <strong>Arrows</strong> to reorder. This order will be used where multiple accounts are listed.
                            <br />- Click <strong>Save Order</strong> to persist changes.
                        </Typography>
                    </Alert>
                </Card>
            </Grid>
        </Grid>
    );
}
